@charset "UTF-8";
.Logo {
  position: fixed;
  top: 0%;
  left: 0%;
  padding: 2rem 2rem; }

.photowall {
  height: 800px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 400px 400px;
  grid-auto-flow: row;
  box-shadow: 0 0 12px 2px lightgray; }
  .photowall .left {
    grid-column: 1;
    grid-row: 1 / 3; }
  .photowall .left,
  .photowall .righttop,
  .photowall .rightbottom {
    display: flex;
    justify-content: center;
    align-items: center; }
    .photowall .left img,
    .photowall .righttop img,
    .photowall .rightbottom img {
      height: 100%;
      width: 100%;
      object-fit: cover; }

.main {
  padding: 5% 5%;
  display: grid;
  grid-template-columns: 7fr 3fr; }
  .main .left {
    display: grid;
    grid-template-columns: 4fr 1fr;
    padding: 1rem 5%; }
    .main .left .roomdetail .descript {
      text-align: justify;
      font-size: 0.9rem; }
    .main .left .roomdetail .slashbox {
      display: flex;
      margin: 5% 0; }
      .main .left .roomdetail .slashbox .slash {
        height: 1rem;
        width: 1rem;
        background: linear-gradient(45deg, transparent 45.5%, deeppink 49.5%, deeppink 50.5%, transparent 55.5%); }
    .main .left .roomdetail .checktime {
      display: flex;
      margin: 10% 0; }
      .main .left .roomdetail .checktime .checkin,
      .main .left .roomdetail .checktime .checkout {
        width: 50%;
        font-size: 1.5rem;
        position: relative; }
        .main .left .roomdetail .checktime .checkin::before,
        .main .left .roomdetail .checktime .checkout::before {
          content: "12";
          height: 100%;
          position: absolute;
          top: -50%;
          left: 0;
          font-size: 1rem; }
      .main .left .roomdetail .checktime .checkin::before {
        content: "Check in"; }
      .main .left .roomdetail .checktime .checkout::before {
        content: "Check out"; }
    .main .left .roomdetail .roomservice {
      width: 100%;
      background-color: #f0f0f0;
      padding: 1rem 1.5rem; }
      .main .left .roomdetail .roomservice td {
        width: 33.33333%;
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        opacity: 0.5;
        padding: 1rem 0; }
        .main .left .roomdetail .roomservice td.use {
          opacity: 1; }
        .main .left .roomdetail .roomservice td img {
          height: 1.5rem;
          margin-right: 1rem; }
    .main .left .price {
      text-align: right; }
      .main .left .price .NormalPrice {
        font-size: 2rem; }
      .main .left .price .HolidayPrice {
        font-size: 1.5rem; }
      .main .left .price .pricetext {
        font-size: 1rem;
        color: #6d7278; }
  .main .right .calendarbox {
    width: 100%;
    height: 320px;
    margin-bottom: 10%;
    box-shadow: 0 0 12px 0px lightgray; }
  .main .right .reserve {
    background-color: #575757;
    border: none;
    color: white;
    font-size: 1.2rem;
    font-family: "Noto Sans TC", "微軟正黑體", sans-serif;
    padding: 1rem 1.5rem; }
